import {useParams} from 'react-router-dom'
import { useIngredientDetailsQuery } from "../../../services/apis/ingredients/ingredients"
import IngredientDetail from '../../ingredient-details/ingredient-details';
import Loader from '../../share/loader/loader';
import ErrorView from '../../share/error/error';

const IngredientPage = () => {
    const { id } = useParams();
    const { data:ingredient, isLoading } = useIngredientDetailsQuery({ingredient_id:id})
    if( isLoading ) return <Loader text=" ..."/>
    if (!ingredient) return <ErrorView text="  "/>
    return <IngredientDetail ingredient={ingredient}/>
}

export default IngredientPage